<template>
  <div class="subject">
    <Loader v-if="subject.loading" />
    <div>
      <md-autocomplete
        value=""
        :disabled="subject.loading"
        :md-options="subject.all"
        @md-selected="item => (subject.selected = item.value)"
        class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
      >
        <label>Предмет</label>
        <template slot="md-autocomplete-item" slot-scope="{ item, term }">
          <md-highlight-text :md-term="term">{{
            item.title
          }}</md-highlight-text>
        </template>
      </md-autocomplete>
      <div
        class="md-layout-item md-xsmall-size-100 md-small-size-60 md-medium-size-40 md-large-size-30 md-xlarge-size-20"
      >
        <div class="md-layout md-alignment-top-space-between">
          <md-datepicker
            name="start"
            v-model="subject.range.start"
            class="md-layout-item md-size-45"
            :md-disabled-dates="subject.loading ? () => true : disabledDates"
          />
          <md-datepicker
            name="end"
            v-model="subject.range.end"
            class="md-layout-item md-size-45"
            :md-disabled-dates="subject.loading ? () => true : disabledDates"
          />
        </div>
      </div>
      <md-button
        class="md-layout-item md-primary"
        :disabled="subject.loading"
        @click="loadSubject"
        >Загрузить</md-button
      >
    </div>
    <md-table v-model="subject.result.assignments" md-card>
      <md-table-row slot="md-table-row" slot-scope="{ item }">
        <md-table-cell
          md-numeric
          :md-label="`Оценка (${subject.result.middleMark})`"
          >{{ item.mark }}</md-table-cell
        >
        <md-table-cell md-label="Тип задания" class="type">{{
          item.type
        }}</md-table-cell>
        <md-table-cell md-label="Тема задания" class="theme">{{
          item.name
        }}</md-table-cell>
        <md-table-cell md-label="Дата выполнения">{{
          item.date | date
        }}</md-table-cell>
        <md-table-cell md-label="Дата выставления">{{
          item.issueDate | date
        }}</md-table-cell>
      </md-table-row>
    </md-table>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import Loader from "@/components/Loader.vue";

export default {
  data: () => ({
    subject: {
      all: [],
      range: {
        start: null,
        end: null
      },
      result: {
        middleMark: 0,
        assignments: []
      },
      selected: "",
      loading: true
    },
    range: {
      get start() {
        return this._start || null;
      },
      set start(val) {
        return (this._start = new Date(val));
      },
      get end() {
        return this._end || null;
      },
      set end(val) {
        return (this._end = new Date(val));
      }
    },
    disabledDates(date) {
      let edges =
        (this.$options.parent.$parent.$parent.$attrs.name == "end" &&
          date <
            this.$options.parent.$parent.$parent.$parent.subject.range.start) ||
        (this.$options.parent.$parent.$parent.$attrs.name == "start" &&
          date >
            this.$options.parent.$parent.$parent.$parent.subject.range.end);
      return (
        edges ||
        date < this.$options.parent.$parent.$parent.$parent.range.start ||
        date > this.$options.parent.$parent.$parent.$parent.range.end
      );
    }
  }),
  methods: {
    ...mapActions(["GetRange", "GetSubjectsID", "GetSubject"]),
    saveLocalRange(id, range) {
      let rangers = {};
      try {
        rangers = JSON.parse(localStorage["vi-sgo-subjects"]);
      } catch (e) {
        localStorage["vi-sgo-subjects"] = {};
      }
      rangers[id] = range;
      localStorage["vi-sgo-subjects"] = JSON.stringify(rangers);
      return range;
    },
    localRange(id) {
      let rangers = {};
      try {
        rangers = JSON.parse(localStorage["vi-sgo-subjects"]);
      } catch (e) {
        localStorage["vi-sgo-subjects"] = {};
      }
      return {
        start: rangers[id]?.start || null,
        end: rangers[id]?.end || null
      };
    },
    loadSubject() {
      this.subject.loading = true;
      this.GetSubject({
        id: this.subject.selected,
        ...this.subject.range
      })
        .then(subject => {
          this.subject.result = subject;
          this.subject.result.assignments.sort((a, b) => {
            return new Date(b.date) - new Date(a.date);
          });
        })
        .catch(err => this.$root.$emit("catch-error", err))
        .finally(() => (this.subject.loading = false));
    },
    showTheme(e) {
      e.target.closest("td.md-table-cell.theme")?.classList.toggle("show");
    }
  },
  watch: {
    "subject.range": {
      handler(val) {
        if (!this.subject.selected) return;
        this.saveLocalRange(this.subject.selected, val);
      },
      deep: true
    },
    "subject.selected"(val) {
      if (!val) return;
      const range = this.localRange(val);
      this.subject.range.start = new Date(range.start || this.range.start);
      this.subject.range.end = new Date(range.end || this.range.end);
    }
  },
  created() {
    document.addEventListener("click", this.showTheme);
    this.subject.loading = true;
    this.GetRange()
      .then(range => {
        this.range.start = range.start;
        this.range.end = range.end;
        return void 0;
      })
      .then(() => this.GetSubjectsID())
      .then(subjects => (this.subject.all = subjects))
      .catch(err => this.$root.$emit("catch-error", err))
      .finally(() => (this.subject.loading = false));
  },
  destroyed() {
    document.removeEventListener("click", this.showTheme);
  },
  components: {
    Loader
  }
};
</script>

<style>
div.subject .md-field.md-layout-item,
div.subject .md-field.md-layout-item + div {
  margin-bottom: 10px;
}
div.subject .md-field.md-layout-item + div + button {
  margin-bottom: 24px;
}
div.subject div.subject .md-datepicker {
  overflow: hidden;
  margin: 0;
}
div.subject .md-card.md-table {
  width: calc(100% - 0.5rem);
}
div.subject td.md-numeric div {
  text-align: center;
}
div.subject td.type div {
  white-space: nowrap;
}
div.subject td.theme div {
  cursor: pointer;
  max-width: 200px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
div.subject td.theme.show div {
  width: 400px;
  max-width: unset;
  white-space: normal;
}
</style>
